@keyframes terminal-cursor {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}
.terminal-cursor::after {
  content: '';
  animation: terminal-cursor 1s infinite;
}
.parallax-slow {
  transform: translateZ(0);
  will-change: transform;
}
.fade-in-up {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease;
}
.fade-in-up.visible {
  opacity: 1;
  transform: translateY(0);
}
